* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Karla", sans-serif;
  text-align: center;
}

header {
  height: 64px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  color: #fff;
  background: linear-gradient(90deg, #672280 1.18%, #a626d3 100%);
}

.header--title {
  font-size: 1.25rem;
}
.header--project {
  font-size: 0.75rem;
}

main {
  padding: 30px;
}

.form {
  display: flex;
  flex-direction: column;
}

input {
  background-color: #fff;
  color: #000;
  padding: 5px;
  margin-right: 20px;
  width: 300px;
  height: 40px;
  border: 0.1px solid gray;
  font-family: "Karla", sans-serif;
}

::placeholder {
  color: #000;
  font-family: "Karla", sans-serif;
}

.input--button {
  align-self: center;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 20px;
  width: 620px;
  height: 45px;
  background-color: #672280;
  border: none;
  font-size: 1rem;
  font-family: "Karla", sans-serif;
}

.header--button {
  align-items: center;
  padding: 7px 10px;
  background-color: #000;
  color: #fff;
  cursor: pointer;
}

.meme--image {
  margin-top: 20px;
    max-width: 100%;
    max-height: 500px;
    border-radius: 3px;
    position: relative;
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}

.meme{
  position: relative;
}
